import React, { useEffect } from "react";
import Aos from "aos";
import "aos/dist/aos.css";
import { admin } from "../Admin/Admin";
import "./Holders.css";

export const Holders = () => {
  useEffect(() => {
    Aos.init({ duration: 1000 });
  }, []);
  return (
    <section className="holders flex-center">
      <h1
        data-aos="fade-down"
        data-aos-offset="200"
        data-aos-easing="ease-in-sine"
        style={{
          color: `${admin.Style.text}`,
          fontFamily: `${admin.Style.sFamily}`,
        }}
      >
        {admin.Content.TotalT}
      </h1>
      <h1
        data-aos="fade-left"
        data-aos-offset="200"
        data-aos-easing="ease-in-sine"
        style={{ color: `${admin.Style.text}` }}
      >
        {admin.Content.TotalBNB}
      </h1>
      <h1
        data-aos="fade-left"
        data-aos-offset="200"
        data-aos-easing="ease-out"
        style={{ color: `${admin.Style.text}` }}
      >
        {admin.Content.TotalUSD}
      </h1>

      <p
        data-aos="fade-up"
        data-aos-offset="100"
        data-aos-easing="ease-in-sine"
        style={{ color: `${admin.Style.text}` }}
      >
        {admin.Content.TotalP}
      </p>

      <div className="flex tradingVolume">
        <h2
          data-aos="flip-up"
          data-aos-offset="100"
          data-aos-easing="ease-in-sine"
          style={{ color: `${admin.Style.text}` }}
        >
          Trading volume ={" "}
        </h2>
        <div
          data-aos="flip-down"
          data-aos-offset="100"
          data-aos-easing="ease-in-sine"
        >
          <span style={{ color: `${admin.Style.text}` }}>$</span>{" "}
          <input
            type="number"
            min="0"
            defaultValue={admin.Content.TradingVolume}
          />
        </div>
      </div>
    </section>
  );
};
