import React, { useEffect } from "react";
import Aos from "aos";
import "aos/dist/aos.css";
import { admin } from "../Admin/Admin";
import { AiOutlineDoubleRight } from "react-icons/ai";
import "./Hero.css";
import { HeroBox } from "./HeroBox/HeroBox";

export const Hero = () => {
  useEffect(() => {
    Aos.init({ duration: 1000 });
  }, []);
  return (
    <section className="hero">
      <h5
        data-aos="fade-down"
        data-aos-anchor="#example-anchor"
        data-aos-offset="500"
        data-aos-duration="500"
        style={{ color: `${admin.Style.text}` }}
      >
        {admin.Content.HeroW}
      </h5>
      <h1
      data-aos="fade-up"
     data-aos-anchor="#example-anchor"
     data-aos-offset="500"
        className="heroTitle"
        style={{
          color: `${admin.Style.text}`,
          fontFamily: `${admin.Style.sFamily}`,
        }}
      >
        {admin.Content.HeroT}
      </h1>

      <div className="flex-between addressBox">
        <input
        data-aos="fade-down"
     data-aos-anchor="#example-anchor"
     data-aos-offset="500"
          type="text"
          defaultValue={admin.Content.UserAddress}
          placeholder={admin.Content.UserAddressPlaceholder}
        />
        <button
        data-aos="fade-up"
     data-aos-anchor="#example-anchor"
     data-aos-offset="500"
     data-aos-duration="500"
         className="flex" style={{ color: `${admin.Style.DarkText}` }}>
          Buy Kodi{" "}
          <span>
            <AiOutlineDoubleRight />
          </span>
        </button>
      </div>

      <div
      data-aos="fade-up"
      data-aos-offset="200"
      data-aos-easing="ease-in-sine"
       className="holdings">
        <h2 style={{ color: `${admin.Style.text}` }}>
          {admin.Content.HoldingT}
        </h2>
        <h1 style={{ color: `${admin.Style.text}` }}>
          {admin.Content.TokenHoldings}
        </h1>
      </div>

      <div 
       className="HeroBoxes">
        <HeroBox title={admin.Content.TBPT} value={admin.Content.TBPValue} />
        <HeroBox title={admin.Content.LPTT} value={admin.Content.LPTValue} />
        <HeroBox title={admin.Content.NPLT} value={admin.Content.NPLValue} />
      </div>

      <button
      data-aos="fade-up"
      data-aos-offset="100"
          data-aos-easing="ease-in-sine"
        className="flex connect"
        style={{ color: `${admin.Style.DarkText}` }}
      >
        {admin.Content.HeroOT}{" "}
        <span>
          <AiOutlineDoubleRight />
        </span>
      </button>
    </section>
  );
};
