import React, { useEffect } from "react";
import Aos from "aos";
import "aos/dist/aos.css";
import { admin } from "../Admin/Admin";
import "./Nav.css";

export const Nav = () => {
  useEffect(() => {
    Aos.init({ duration: 1000 });
  }, []);
  return (
    <nav className="flex-between">
      <img
        data-aos="fade-right"
        data-aos-offset="300"
        data-aos-easing="ease-in-sine"
        className="logo"
        src={admin.Style.logo}
        alt="Logo"
      />
      <div className="flex navLink">
        <button
        data-aos="fade-down"
          data-aos-offset="300"
          data-aos-easing="ease-in-sine"
        >Contact</button>
        <button
        data-aos="fade-up"
          data-aos-offset="300"
          data-aos-easing="ease-in-sine"
        >Return to Home</button>
      </div>
    </nav>
  );
};
