import React, { useEffect } from "react";
import Aos from "aos";
import "aos/dist/aos.css";
import { admin } from '../../Admin/Admin';
import './HeroBox.css';

export const HeroBox = ({title , value}) => {
    useEffect(() => {
        Aos.init({ duration: 1000 });
      }, []);
    return (
        <div 
        data-aos="flip-right"
          data-aos-offset="200"
          data-aos-easing="ease-in-sine"
         className="HeroBox">
            <h1 style={{color: `${admin.Style.sText}`}}>{title}</h1>
            <h2 style={{color: `${admin.Style.text}`}}>{value}</h2>
        </div>
    )
}
